@import "../../../../../../../Assets/colorPallete/colorPallete.scss";


.root {
    display: flex;
    width: 100%;
    flex-direction: column;
    .title-header-container {
      display: flex;
      align-items: center;
      .label {
        padding-bottom: 5px;
        span {
          font-size: 8;
        }
      }
    }
  
    .wrapper {
      .read-only-value {
        font-weight: unset;
        padding: 5px;
      }
  
      .select-container {
        flex: 1;
        border-radius: 10px;
        border: 2px solid lightgrey; 
        background-color: $color-1;
        display: flex;
  
        .select {
          padding: 0;
          padding-left: 1em;
        }
  
        .input {
          width: 100%;
          .select {
            padding: 10px;
            padding-right: 20px;
            text-align: left;
          }
        }
  
        // Errors
        &.has-error {
          border: 2px solid red;
          margin-bottom: unset;
        }
  
        &.disabled {
          background-color: rgb(223, 223, 223);
        }
      }
      span.error {
        padding-top: 1px;
        color: red;
        font-size:12px;
      }
    }
  }
  