@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5.8em;
    .container {
        width: 100% !important;
        .tableContainer {
            background-color: $color-13;
            .table {
                width: 100%;
                background: white;
                .tableHeader {
                    padding: 10px 0;
                    background: $color-2 !important;
                    width: 100%;
                    .tableHeaderCell {
                        margin: 0;
                        height: 100%;
                        margin-top: 1em;
                        background: $color-2 !important;
                        .tableHeaderTitle {
                            color: black;
                            margin-right: 8px;
                            font-size: 0.8vw;
                            
                            -webkit-touch-callout: none; 
                            -webkit-user-select: none; 
                            -khtml-user-select: none; 
                            -moz-user-select: none; 
                            -ms-user-select: none; 
                                user-select: none; 
                        }
                        .tableHeaderSortIcon {
                            &:hover {
                                cursor: pointer;
                            }
                        }
                        .tableHeaderAutoComplete {
                            width: 100%;
                            background: #f5f7fb;
                        }
                    }
                }
            }

            .TopButton{
                display: inline-flex;
                width: 100%;
                height: auto;
                justify-content: flex-end;
                
                .updateButtonDiv {
                    padding-right: 10px;
                    margin-bottom: 10px;
                    display: flex;
                    justify-content: flex-end;
                    .updateButton {
                        margin-top: 2em;
                        background-color: $color-9;
                        color: white;
                        text-transform: initial;
                        padding-left: 1.5em;
                        padding-right: 1.5em;
                        font-size: 0.8vw;
                    }
                }
    
                .addButtonDiv{
                    padding-right: 10px;
                    margin-bottom: 10px;
                    display: flex;
                    justify-content: flex-end;
                    .addButton {
                        margin-top: 2em;
                        background-color: $color-8;
                        color: white;
                        text-transform: initial;
                        padding-left: 1.5em;
                        padding-right: 1.5em;
                        font-size: 0.8vw;
                    }
                }
    
            }
         
            .tablePagination {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 10px;
                padding-bottom: 30px;
                padding-left: 10px;
                padding-right: 10px;
                .options {
                    display: flex;
                    align-items: center;
                }
            }
        }
        .buttonsGroup {
            padding-right: 7px;
            margin-top: 8px;
            display: flex;
            justify-content: flex-end;
            .btnRed {
                margin-right: 10px;
                background-color: $color-10;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;
            }
            .btnGreen {
                background-color: $color-9;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;
            }

            .disable{
                background-color: lightgray;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;  
            }
        }
    }
}
